.mini-header {
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(/static/media/banner.3382a723.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; 
    height: 400px;
}

.mini-header .header-text {
    position: absolute;
    top: 50%;
    transform: translateY(-55%);
    text-align: center;
    width: 100%;
}

.mini-header .header-text h1 {
    font-weight: 500;
    font-size: 30px;
    line-height: 54px;
    letter-spacing: 1.4px;
    color: #fff;
}

.mini-header .header-text p {
    font-weight: 400;
    font-size: 15px;
    color: #fff;
    line-height: 28px;
    letter-spacing: 0.25px;
    margin-bottom: -70px;
    position: relative;
}

@media (max-width: 991px) {
    .mini-header .header-text {
        top: 65% !important;
        transform: perspective(1px) translateY(-60%) !important;
        text-align: center;
    }
    .mini-header .header-text h1 {
        text-align: center;
        color: #fff;
        margin-bottom: 15px;
    }
    .mini-header .header-text h1 span {
        color: #fff;
    }
