.paymentForm {
    display: grid;
    grid-column-gap: 10px;
    grid-template-columns: 305px;
    grid-template-rows: 90px 90px 90px;
    max-width: 310px;
    margin-left: 40px;
    margin-top: 40px;
    height: 600px;

    border-radius: 10px;

    color: #707070;
    background-color: rgb(245, 245, 245);


    box-shadow: 1px -1px 0px 7px rgba(242,242,242,0.75);
    -webkit-box-shadow: 1px -1px 0px 7px rgba(242,242,242,0.75);
    -moz-box-shadow: 1px -1px 0px 7px rgba(242,242,242,0.75);
}

.paymentForm label {
    font-size: 13px;
    margin-left: 5px;
}

.paymentForm input {
    padding: 5px;
    font-size: 16px;
    width: 98%;
    border-radius: 6px;
    border: 1px solid #dcdcdc;
    margin-left: 5px;
    outline: none;
}

.paymentForm input:hover {
    border: 1px solid #744894;
}

